.header {
  max-width: 880px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 45px 200px 36px 200px;
  padding: 0 0 40px 0;
  border-bottom: 1px solid rgba(84,84,84,0.7);
}

@media screen and (max-width: 631px) {
  .header {
    margin: 0 0 38px 0;
    padding: 28px 27px 32px 27px;
    justify-content: center;
  }
}