.close-button-tooltipe {
    height: 40px;
    width: 40px;
    font-weight: normal;
    position: absolute;
    right: -40px;
    top: -40px;
    cursor: pointer;
    background-image: url(../../images/Close_Icon.svg);
    background-color: transparent;
    background-size: cover;
    background-position: top;
    background-repeat: no-repeat;
    border: none;
  }
  
  .close-button-tooltipe:hover {
    opacity: 0.6;
  }
  
  @media screen and (max-width: 879px) {
    .close-button-tooltipe {
      background-image: url(../../images/Close_Icon-mobile.svg);
      height: 26px;
      width: 26px;
      right: 131px;
      top: -40px;
      border: 0;
    }
  }